@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
@import url(https://fonts.sandbox.google.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #141414;
  min-height: 100vh;
  min-width: 100vw;
}

.App {
  text-align: center;
  /* position: relative; */
  /* overflow: auto; */
  height: 100vh;
  width: 100vw;
}

.actors {
  display: grid;
  grid-auto-flow: column;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

li {
  list-style-type: none;
}

.new_actor {
  cursor: pointer;
}

.new_actor:hover {
  opacity: 1;
}


.arrowGraphic {
  width: 100vw;
  height: 100vh;
  position: relative;
  top: 0;
  left: 0;
}

.header {
  z-index: 98;
  background: #141414;
  /* background: linear-gradient(180deg, hsl(227, 59%, 16%) 90%, rgba(0, 212, 255, 0) 100%); */
  width: 100vw;
  position: fixed;
  top: 0;
}

.margin-left {
  margin-left: 1rem;
}

.arrow-message {
  color: white;
  margin: 0;
  padding: 0 20px;
}

.arrow-edit {
  right: calc(50% - 66px);
  top: -34px;
  position: absolute;
  display: flex;
  align-items: flex-end;
  grid-gap: 6px;
  gap: 6px;
  background-color: rgba(32, 32, 32, 0.9);
  padding: 4px 8px;
  border: radius 2px;
  border: 1px solid rgba(117, 117, 117, 0.4);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2), 0px 2px 21px rgba(0, 0, 0, 0.2);
  z-index: 999;
}

.arrow-emoji {
  font-size: 24px;
  margin: 0;
}

.add-step-btn {
  position: fixed;
  bottom: 16px;
  right: 16px;
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}

input:focus:-ms-input-placeholder {
  color: transparent;
}

input:focus::placeholder {
  color: transparent;
}

.svg-container {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-gap: 0px;
  gap: 0px;
  cursor: move;
  z-index: 101;
}

.ordering {
  position: relative;
  display: flex;
  width: auto;
  grid-gap: 8px;
  gap: 8px;
  opacity: 0;
  align-items: center;
  justify-content: center;
  transform: translateY(42px);
}

.ordering:hover {
  opacity: 1;
  z-index: 999;
}

.ordering>button {
  background: transparent;
  border: none;
  box-shadow: none;
  font-size: 16px;
  padding: 0;
}


.add-step-btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  text-align: center;
  background: #FAC748;
  color: #1D2F6F;
  font-size: 1em;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 800;
  padding: 1em;
  border-radius: 999px;
  line-height: 1;
  border: none;
}

.arrow-list-itme {
  width: 100vw;
  background-color: aliceblue;
  color: aquamarine;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.arrow-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.overflowthis {
  position: absolute;
  overflow: auto;
  width: 100vw;
  height: calc(100vh - 64px);
  margin-top: 64px;
  padding: 16px 0px;
}

.actor-txt {
  z-index: 999;
}

.actors-container {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  position: relative;
}

.non-draggable-area {
  color: #141414;
  cursor: default;
  width: 100%;
  z-index: 101;
  position: absolute;
  left: 0px;
  width: 100%;
}

.non-arrow-draggable {
  background: rgba(124, 204, 155, 0.05);
  color: white;
}

.non-arrow-draggable:hover {
  opacity: 0.05;
  background: rgba(0, 0, 0, 0.1);
}

.svg-container .arrow-edit {
  visibility: hidden;
}

.svg-container:hover .arrow-edit {
  visibility: visible;
}

.actor-bg {
  position: fixed;
  top: 64px;
  background: linear-gradient(rgba(20, 20, 20, 1), rgba(20, 20, 20, 0.7));
  width: 100vw;
  height: 72px;
  z-index: 888;
}

.page {
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.login-component {
  padding: 16px;
  display: flex;
  flex-direction: column;
  min-width: 600px;
  background-color: #fff;
  grid-gap: 16px;
  gap: 16px;
  padding-top: 24px;
}

.login-form {
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
}

.login-inputs {
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
}

.login-buttons {
  display: flex;
  justify-content: flex-end;
  grid-gap: 16px;
  gap: 16px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.flows-content {
    display: flex;
    flex-direction: column;
    grid-gap: 32px;
    gap: 32px;
}

.toolbar {
    display: flex;
    justify-content: space-between;
    padding: 16px;
    align-items: center;
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px 16px;
    font-size: 14px;
    font-weight: 400;
}

.flows-content {
    max-width: 1280px;
    margin: 64px auto;
}

.create-flow-form {
    display: flex;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
}



.create-label {
    color: white;
}


.text-input {
    width: 320px;
}

.text-white {
    color: white;
    margin: 0 !important;
}
.material-symbols-outlined {
  color: #fff;
  font-variation-settings:
    'FILL'0,
    'wght'400,
    'GRAD'0,
    'opsz'48
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 16px;
  z-index: 999;
}

.left-side {
  display: flex;
  align-items: center;
  grid-gap: 4px;
  gap: 4px;
}

.right-side {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  grid-gap: 16px;
  gap: 16px;
}

.header-account {
  display: flex;
  grid-gap: 6px;
  gap: 6px;
  align-items: center;
}

.home-link {
  height: 24px;
  width: 24px;
}
.card-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 12px;
    gap: 12px;
    padding: 16px;
}

.card {
    display: grid;
    grid-template-columns: 1fr 240px 240px 24px;
    align-items: center;
    padding: 16px;
    background: #1c1c1c;
    border: 1px solid rgba(117, 117, 117, 0.4);
    box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.2), 0px 2px 21px rgba(0, 0, 0, 0.2);
    border-radius: 7px;
}

.card p {
    color: #fff;
}

.flow-name {
    font-size: 18px;
    font-weight: 600;
}

.flows-heading {
    border: none;
    box-shadow: none;
    background: none;
    padding: 0px 16px;
}

.flows-heading>p {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    opacity: 0.5;
    margin-bottom: 4px;
}
.actor-txt {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    text-align: center;
    background: #FAC748;
    color: #1D2F6F;
    font-size: 1em;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 800;
    padding: 1em;
    border-radius: 999px;
    line-height: 1;
    border: none;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    z-index: 999;
}

.actor-txt:focus {
    outline: none;
}

.autor-container {
    scroll-snap-align: center;
}

.thread-add-btn {
    z-index: 9999;
}
.thread-container {
    /* top: 50px; */
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.vl {
    border-left: 1px solid hsla(233, 92%, 75%, 0.48);
    height: 96px;
}

.Dropable-actor {
    border-left: 80px solid rgb(0 0 0 / 0%);
    height: 100vh;
    margin-top: 50px;
}

.Dropable-list {
    z-index: 100;
}

.thread-add-btn {
    border-radius: 999px;
    position: absolute;
    bottom: 0;
    height: 16px;
    width: 16px;
    font-size: 12px;
    line-height: 1;
    background: rgba(255, 255, 255, 0.596);
    display: block;
    color: #1D2F6F;
    font-weight: 800;
    border: none;
    opacity: 0.05;
    transition-property: opacity;
    transition-duration: 0.3s;
}

.thread-add-btn:hover {
    opacity: 1;
    border: none;
}

.active {
    visibility: hidden;
    opacity: 0.1;
    border: none;
}
