.step-form {
    z-index: 999;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 16px;
    gap: 16px;
    position: fixed;
    background: white;
    padding: 16px;
    border-radius: 12px;
    width: 608px;
    top: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.field {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: -webkit-fill-available;
    grid-gap: 8px;
    gap: 8px
}

.value {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: -webkit-fill-available;
}

.list {
    grid-gap: 12px;
    gap: 12px;
    flex-wrap: wrap;
}

.textbox {
    width: -webkit-fill-available;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    min-height: 4em;
    background: white;
    border: 1px solid #bdbdbd;
    border-radius: 16px;
    font-family: Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1em;
    line-height: 1.5;
    padding: 16px;
    overflow: auto;
    outline: none;
    box-shadow: none;
    resize: none;
}

.emoji-picker {
    height: 200px;
    overflow: scroll;
}

.emoji-chip>img {
    height: 2em;
    width: 2em;
}

.add-modal {
    position: fixed;
    /* Stay in place */
    z-index: 9998;
    /* Sit on top */
    left: 0;
    top: 0;
    width: 100%;
    /* Full width */
    height: 100%;
    /* Full height */
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.6);
    /* Black w/ opacity */
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);

}

input[type=radio] {
    display: none;
}
input[type=radio]:checked ~ label {
    background-color: #FAC748;
    border: 1px solid #FAC748;
  }

.radio-chip{
    padding: 8px 16px;
    border: 1px solid #bdbdbd;
    border-radius: 999px;
    color:#1D2F6F;
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 800;
}

.chip{
    display: flex;
}

.radio-emo-chip{
    padding: 6px;
    font-size: 32px;
    height: 36px;
    width: 36px;
    border: 1px solid #FAC748;
    border-radius: 999px;
}

.visible{
    cursor: pointer;
}

.disabled{
    cursor: not-allowed;
    opacity: 0.5;
}
